import React, {useEffect} from 'react'; 
import axios from 'axios';
import { Route, BrowserRouter as Router } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import Footer from '../src/Scripts/Components/Footer';
import Header from '../src/Scripts/Components/Header';
import NavigationMobile from '../src/Scripts/Components/NavigationMobile';
import Start from '../src/Scripts/Containers/Start';
import Home from './Scripts/Components/Home';
import ProductList from '../src/Scripts/Components/ProductList';
import News from '../src/Scripts/Components/News';
import Search from '../src/Scripts/Components/Search';
import Favorites from '../src/Scripts/Components/Favorites';
import MyPages from '../src/Scripts/Components/MyPages';
import Checkout from '../src/Scripts/Components/Checkout';
import Inspiration from '../src/Scripts/Components/Inspiration';
import TheRoom from '../src/Scripts/Components/TheRoom';
import { getProducts } from './Scripts/Actions';
import { getCart } from './Scripts/Actions';

function App() {

	const dispatch = useDispatch();
	
	useEffect(() => {
		getAllProducts();
		getTotalCart();
	}, [])

	async function getAllProducts() {
		await axios.get('/api/allproducts')
			.then(res => {
				dispatch(getProducts(res.data));
			})
			.catch(err => {
				console.log('Something went wrong', err)
			})
	}

	async function getTotalCart() {
		await axios.get('/api/cart')
			.then(res => {
				dispatch(getCart(res.data));
			})
			.catch(err => {
				console.log('Something went wrong', err)
			})
	}

	return (
		<div>
			<Router>
				<div>
					<Header />
					<Search/>
					<Route path="/" exact component={Start} />
					<Route path="/home" exact component={Home} />
					<Route path="/browse" component={ProductList} />
					<Route path="/news" component={News} />
					<Route path="/favorites" component={Favorites} />
					<Route path="/mypages" component={MyPages} />
					<Route path="/checkout" component={Checkout} />
					<Route path="/inspiration" component={Inspiration} />
					<Route path="/theroom" component={TheRoom} />
					<NavigationMobile />
					<Footer />
				</div>
			</Router>
		</div>
	);
}

export default App;